.news {
  width: 100%;
}

.news__wrapper {
  max-width: 1440px;
  margin: 0 auto;
  padding: 124px 6.9444444444% 60px;

  @media (min-width: 1440px) {
    padding-left: 100px;
    padding-right: 100px;
  }

  @media (max-width: 1300px) {
    padding-left: 5%;
    padding-right: 5%;
  }

  @include vp-1199 {
    padding: 99px 45px 50px;
  }

  @include vp-767 {
    padding: 68px 15px 20px;
  }
}

.news__wrapper h2 {
  margin: 0;
  margin-bottom: 46px;
  padding: 0;
  text-align: center;
  color: $color-light-blue;
  font-size: 50px;
  font-weight: 400;
  line-height: 50px;

  @include vp-1199 {
    font-size: 34px;
    font-weight: 400;
    line-height: 50px;
    margin-bottom: 31px;
  }

  @include vp-767 {
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px; /* 100% */
    margin-bottom: 28px;
  }
}

.news__buttons-wrapper {
  margin: 0 auto;
  margin-bottom: 40px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  width: max-content;
  max-width: 1100px;

  @include vp-1199 {
    margin-bottom: 30px;
    max-width: 700px;
  }

  @include vp-767 {
    margin-bottom: 25px;
    overflow-x: auto;
    flex-wrap: nowrap;
    justify-content: unset;
    max-width: 100%;

    &::-webkit-scrollbar {
      display: none;
    }

    &::-webkit-scrollbar-thumb {
      display: none;
    }
  }
}

.news__button {
  margin: 0;
  padding: 12px 26px;
  margin-left: 15px;
  margin-right: 15px;
  color: rgba(49, 109, 194, 0.6);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  letter-spacing: 0.28px;
  text-transform: uppercase;
  border-radius: 20px;

  &:hover {
    color: $color-light-blue;
  }

  &:focus-visible {
    color: $color-light-blue;
    outline: none;
  }

  @include vp-1199 {
    padding: 12px 19px;
    margin: 0 5px;
  }

  @include vp-767 {
    font-size: 12px;
    font-weight: 500;
    line-height: 12px;
    letter-spacing: 0.24px;
    padding: 8px 14px;
  }
}

.news__button--active {
  padding: 11px 25px;
  border: 1px solid $color-light-blue;
  color: $color-light-blue;

  @include vp-1199 {
    padding: 11px 18px;
  }

  @include vp-767 {
    padding: 7px 13px;

  }
}

.news__swiper-wrapper {
  @include vp-1199 {
    height: 104.1666666666667vw;
  }

  @include vp-767 {
    height: 203.125vw;
  }
}

.news__swiper {
  margin-bottom: 40px;

  @include vp-1199 {
    height: 95.05208333333333vw;
    margin-bottom: 30px;
  }

  @include vp-767 {
    height: 184.375vw;
    margin-bottom: 24px;
  }
}

.news__swiper-slide {
  max-width: 286px;
  min-height: 400px;
  height: initial;
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  padding-top: 191px;

  @include vp-1199 {
    max-width: unset;
    min-height: unset;
    height: 45.57291666666667vw;
  }

  @include vp-767 {
    max-height: unset;
    height: 75vw;
    border-radius: 12px;
  }
}

.news__slide-img {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;

  @media (min-width: 1200px) {
    object-fit: cover;
  }
}

.news__slide-img img {
  width: 100%;
  height: 100%;
}

.news__content-wrapper {
  position: relative;
  z-index: 2;
  background-color: transparent;
}

.news__interactives-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  min-height: 32px;
}

.news__date-wrapper {
  background-color: rgba(240, 239, 233, 1);
  padding: 2px 12px 0px 0;
  border-radius: 0px 12px 0px 0px;
  position: relative;
  max-width: 79px;

  &::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    background-image: url(../img/svg/angle-sand.svg);
    background-repeat: no-repeat;
    background-size: 16px 16px;
    left: 0;
    top: -16px;
  }

  &::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    background-image: url(../img/svg/angle-sand.svg);
    background-repeat: no-repeat;
    background-size: 16px 16px;
    bottom: 0;
    right: -16px;
  }

  @include vp-1199 {
    min-height: 3.515625vw;
    padding: 2px 12px 3px 0;
    max-width: unset;
    width: 24.3827160493827%;
    display: flex;
    align-items: center;
  }

  @include vp-767 {
    padding-top: 3px;
    min-height: 8.4375vw;
    max-width: unset;
    width: 29.65779467680608%;
    display: flex;
    align-items: center;
  }
}

.news__date-wrapper span {
  color: $color-light-blue;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px; /* 100% */
  letter-spacing: 0.12px;
  text-transform: uppercase;

  @include vp-1199 {
    font-size: 1.5625vw;
    line-height: 1.5625vw;
  }

  @include vp-767 {
    font-size: 3.75vw;
    line-height: 3.75vw;
  }
}

.news__heading-wrapper {
  background-color: rgba(240, 239, 233, 1);
  border-radius: 0px 12px 0px 0px;
  padding: 5px 4px 0px 0px;;
  max-width: 214px;
  min-height: 57px;
  position: relative;
  margin-top: -1px;

  &::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    background-image: url(../img/svg/angle-sand.svg);
    background-repeat: no-repeat;
    background-size: 16px 16px;
    bottom: 0;
    right: -16px;
  }

  @include vp-1199 {
    min-height: 7.03125vw;
    max-width: unset;
    width: 63.8888888888889%;
    display: flex;
    align-items: center;
  }

  @include vp-767 {
    padding: 5px 4px 0 0;
    max-width: unset;
    min-width: unset;
    width: 68.966%;
    min-height: 15.625vw;
    display: flex;
    align-items: center;
  }
}

.news__heading-wrapper h3 {
  margin: 0;
  padding: 0;
  color: $color-light-blue;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px; /* 108.333% */
  max-width: 204px;

  @include vp-1199 {
    font-size: 2.604166666666667vw;
    line-height: 3.125vw; /* 120% */
  }

  @include vp-767 {
    font-size: 5vw;
    line-height: 5.625vw;
    max-width: unset;
  }
}

.news__text-wrapper {
  margin: 0;
  padding: 0 18px 4px 0;
  background-color: rgba(240, 239, 233, 1);
  min-height: 57px;
  display: flex;
  justify-content: space-between;
  max-width: 240px;
  min-width: 240px;
  border-radius: 0px 12px 0 0;
  align-items: center;
  position: relative;
  justify-content: space-between;
  margin-top: -1px;

  &::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    background-image: url(../img/svg/angle-sand.svg);
    background-repeat: no-repeat;
    background-size: 16px 16px;
    bottom: 0;
    right: -16px;
  }

  @include vp-1199 {
    padding: 0 18px 9px 0;
    min-height: 7.421875vw;
    max-width: unset;
    min-width: unset;
    width: 74.0740740740741%;
  }

  @include vp-767 {
    max-width: unset;
    min-width: unset;
    width: 90.68965517241379%;
    min-height: 16.25vw;
    padding: 7px 10px 4px 0;
  }
}

.news__text-wrapper p {
  margin: 0;
  padding: 0;
  max-width: 176px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;

  @include vp-1199 {
    line-height: 2.604166666666667vw;
    font-size: 1.822916666666667vw;
    max-width: 76.333333%;
  }

  @include vp-767 {
    font-size: 3.75vw;
    line-height: 4.375vw;
  }
}

.news__swiper-slide--active {
  @media (min-width: 1200px) {
    max-width: 604px;
    min-width: 48.70967741935484%;
    padding-top: 250px;
  }

  @include vp-767 {
    height: 103.125vw;
  }
}

.news__swiper-slide--active .news__heading-wrapper {
  @media (min-width: 1200px) {
    max-width: 291px;
    padding: 14px 4px 1px 0;
  }

  @include vp-1199 {
    max-width: unset;
  }
}

.news__swiper-slide--active h3 {
  @media (min-width: 1200px) {
    max-width: 273px;
  }
}

.news__swiper-slide--active .news__text-wrapper {
  @media (min-width: 1200px) {
    max-width: 389px;
    min-width: 389px;
  }

  @include vp-1199 {
    min-height: 97px;
  }

  @include vp-767 {
    min-height: 64px;
  }
}

.news__swiper-slide--active p {
  @media (min-width: 1200px) {
    max-width: 325px;
  }

  @include vp-767 {
    max-width: 80.9885931558935%;
  }
}

.news__swiper-pagination {
  max-width: 196px;
  position: unset;
  display: flex;
  justify-content: space-between;

  @include vp-767 {
    max-width: 55.17241379310345%;
  }
}

.news__swiper-button {
  position: unset;
  left: unset;
  right: unset;
  bottom: unset;
  top: unset;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  stroke: $color-light-blue;

  &:hover {
    background-color: $color-default-white;
  }

  &:focus-visible {
    background-color: $color-default-white;
    outline: none;
  }

  &:active {
    background-color: $color-default-white;
    stroke: rgba(49, 109, 194, 0.7);
  }

  &::after {
    content: unset;
  }
}

.news__swiper-bullet {
  width: 32px;
  height: 32px;
  display: inline-flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  color: rgba(49, 109, 194, 0.5);
  opacity: 1;
  background: transparent;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px; /* 125% */
  margin-left: 10px;
  margin-right: 10px;

  &:hover {
    border: 1px solid $color-light-blue;
  }

  &:focus-visible {
    outline: none;
    border: 1px solid $color-light-blue;
  }

  &:active {
    border: 1px solid transparent;
    background-color: rgba(39, 86, 153, 1);
    color: $color-default-white;
  }

  @include vp-767 {
    width: 26px;
    height: 26px;
    font-size: 14px;
    line-height: 14px;
  }
}

.swiper-pagination-bullet-active {
  color: $color-default-white;
  background: $color-light-blue;
}
